body {
  position: relative;
  width: 90%;
  margin: 10px auto;
  
  font-family: 'Arial';
}
.container-md {
  border: 1px dashed #5d5fef ;
  background-color: #f7f7fe;
}

/* Dark Theme */
body.darktheme, body.darktheme .container-md {
  color: rgb(255, 255, 255) !important;
  background-color: rgb(40, 44, 52) !important;
}

body.darktheme input[type=text],
body.darktheme .form-control:focus,
body.darktheme textarea,
body.darktheme .CodeMirror-gutter,
body.darktheme .CodeMirror {
  background: rgb(40, 44, 52) !important;
  border: 2px solid #eee !important;
  color: rgb(255, 255, 255);
}

/* Dark Theme end */

#content1 {
  resize: none;
}

