.CodeMirror {
  font-size: 13px;
  border: 1px solid rgb(118, 118, 118);
  margin-bottom: 5px;
}

.cm-s-default .cm-comment {
  color: #a50;
}

.cm-s-default .cm-tag,
.cm-s-default .cm-bracket {
  color: #170;
}

.cm-s-default .cm-attribute {
  color: #00c;
}

.cm-s-default .cm-string {
  color: #a11;
}